.intro{
  color: #FFFFFF;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family: 'Aboreto', cursive;
  margin-top: 250px;
  margin-bottom: 390px;
}

.intro h1{
  margin-left: 120px;
  font-size: 60px;
}

.intro img{
  width: 20%;
  height: 20%;
  padding-right: 80px;
}

@media (max-width: 800px){
  .intro{
    flex-direction: column-reverse;
  }

  .intro img{
    padding: 0;
    width: 25%;
    padding-bottom: 40px;
  }

  .intro h1{
    font-size: 40px;
    margin: 0;
  }
}

@media (max-width: 650px){
  .intro img{
    width: 50%;
  }

  .intro h1 {
    font-size: 30px;
  }
}

@media (max-width: 450px){
  .intro h1 {
    font-size: 23px;
  }
}
